  font-family: "Montserrat";
  src:
    url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format("woff2"),
    url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format("woff");
  font-weight: normal;
  font-style: normal;
}

body {
  font-family: "Montserrat", system-ui, sans-serif;
  overflow-x: hidden;
  display: grid;
  gap: 3rem;
}

section {
  padding: 3rem;
  width: 100vw;
}

main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 3rem;
}

.bg {
  position: absolute;
  top: 0;
  object-fit: cover;
  background-color: rgb(19, 115, 206);
  height: calc(80% - 1vw);
  width: 100vw;
  transform: translateY(-40%) skewY(-10deg);
  z-index: -1;
}
.profile-wrapper {
  display: grid;
  place-content: center;
  gap: 3rem;
  place-items: center;
}

.profile {
  /* float: left; */
  text-align: center;
  color: white;
  position: relative;
  max-width: 25rem;
}

.profile img {
  height: auto;
  width: 60rem;
  object-fit: cover;
}

.profile h1 {
  width: 100%;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: start;
  font-size: clamp(3rem, 3vw, 10rem);
  line-height: 1;
  bottom: 1rem;
}

.about-me {
  font-size: 2rem;
  text-align: justify;
}

.about-me h2 {
  text-align: center;
}

.icon {
  height: auto;
  width: 4rem;
}

.socials {
  display: flex;
  gap: 2rem;
}

.projects-wrapper h3 {
  margin-bottom: 3rem;
  font-size: 3rem;
  text-align: center;
}

.projects {
  display: grid;
  place-content: center;
  place-items: center;
  grid-template-columns: repeat(auto-fit, minmax(300px, 500px));
  gap: 2rem;
}

.project {
  display: flex;
  flex-direction: column;
}

.project-pic img {
  height: 20rem;
}

.project-name {
  display: flex;
  justify-content: space-around;
}

.project-links {
  display: flex;
  gap: 1rem;
}

.project-links img {
  height: 1.5rem;
}

.project-description {
  border: 1px solid black;
  padding: 1rem;
}

.project-description p {
  text-align: center;
}

footer {
  background-color: rgb(19, 115, 206);
  color: white;
  padding: 0rem;
}

section#contactMe {
  padding: 0;
}

.contact-details {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 3rem;
}

.contact-details h4 {
  font-size: 2.4rem;
}

footer img {
  width: 100vw;
}

@media (min-width: 900px) {
  .profile-wrapper {
    padding: 10rem 5rem;
    display: block;
    max-width: 90vw;
  }

  .profile {
    float: left;
    z-index: 1;
    margin-right: 5rem;
    padding: 2rem;
  }

  .profile img {
    transform: scale(1.5);
  }

  .profile h1 {
    top: 0;
    transform: translateX(20rem);
  }

  .about-me {
    padding: 10rem 2rem;
    width: 80vw;
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    transform: translateY(7rem);
  }

  .profile-wrapper .socials {
    display: none;
  }

  section#contactMe {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 3rem;
  }

  #contactMe .pic {
    max-width: 20rem;
  }
}

@media (min-width: 1600px) {
  .profile-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0;
  }

  .profile {
    height: 100%;
    margin: 0;
  }

  .profile img {
    height: 100%;
    transform: scale(1.3);
  }

  .profile h1 {
    height: min-content;
    width: 100%;

    transform: translateX(-2rem);

    margin-top: auto;
  }

  .about-me {
    padding: 2rem;
    padding-left: 3rem;
    transform: translateY(0);
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  }
}
